$breakpoints: (
   'phone': 400px,
   'phone-wide': 480px,
   'phablet': 560px,
   'tablet-small': 640px,
   'tablet': 768px,
   'tablet-middle':860px,
   'tablet-wide': 1024px,
   'desktop': 1248px,
   'desktop-wide': 1440px
);

@mixin mq($width, $type: min) {
   @if map-has-key($breakpoints, $width) {
      $width: map-get($breakpoints, $width);
      @if $type== max {
         $width: $width - 1px;
      }
      @media only screen and (#{$type}-width: $width) {
         @content;
      }
   }
}