@font-face {
   font-family: 'Ubuntu';
   src: url('/fonts/Ubuntu/Ubuntu-Regular.ttf');
}
@font-face {
   font-family: 'Maven_Pro';
   src: url('/fonts/Maven_Pro/MavenPro-Regular.ttf');
}

html {
   font: 12px/1.5 'Ubuntu', -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
   color: #555555;
}

@media screen and (min-width: 500px){
   html{ font-size: 14px; }
}
@media screen and (min-width: 700px){
   html{ font-size: 16px; }
}
@media screen and (min-width: 900px){
   html{ font-size: 18px; }
}
@media screen and (min-width: 1600px){
   html{ font-size: 20px; }
}
body {
   max-width: 1600px;
   margin: 0 auto;
}
body, td, th, form, textarea, input, option, select {
   font: inherit;
} 
h1, h2, h3, h4, h5, h6 {
   line-height: 1.2;
   padding-bottom: 0.5em;
}
$headings: (h1: 2.442em, h2: 1.953em, h3: 1.563em, h4: 1.25em, h5: 1em, h6: 1em);

@each $heading, $size in $headings{
   #{$heading} { font-size: $size; }
}
h2 {font-weight: normal;}
h6 { font-weight: bold; }

p, ul, ol {
   padding-bottom: 1.5em; 
}
ul, ol {
   padding-left: 2em;
}
li {
   list-style-position: inside;
}